html{
    min-height: 100%;
}

body{
    padding: 0;
    margin: 0;
    background-color: rgb(25, 53, 212);
    color: snow;
    font-family: Arial, Helvetica, sans-serif;
}

#root{
    width: 100%;
    height: 100%;
}

.app{
    display: flex;
    align-items: center;
    width: 100%;
    flex-flow: column;
}

.app > *{
    min-width: 90%;
}

/* TODO: Make this cleaner. */
.app > .folder-creation-window, .popup-window{
    min-width: 0;
}
/* .drop-area{
    background-color: snow;
    width: 90%;
    min-height: 50%;
    max-height: 20em;
} */

.drag-on{
    background-color: rgb(200, 200, 200);
}

.path-container{
    display: flex;
    align-items: center;
    padding: .2rem;
    margin: .5rem;

    border-top: 2px solid rgb(122, 112, 255);
    border-bottom: 2px solid  rgb(122, 112, 255);
}

.path-title{
    margin-right:.5rem
}

.path-actual{
    color: rgb(20, 20, 20);
    padding: 0.2rem;
    background-color: snow;
}

ul{
    list-style: none;
}