.background-fill{
    position: absolute;
    background-color: rgba(0, 0, 0, 0.473);
    width: 100%;
    height: 100%;
}

.profile-window{
    position: absolute;
    
    background-color: rgb(25, 53, 212);
    
    left:50%;
    top:50%;

    margin-left: -16rem;
    margin-top: -11rem;

    height: 22rem;
    width: 32rem;
}

.profile-block-container{
    display: flex;
    flex-flow: row;
    
    /* background-color: aliceblue; */
    
    width: 100%;
    height: 90%;
}

.profile-window-block{
    /* background-color: pink; */
    height: 100%;
    width: 50%;
}

.profile-window-block-title{
    display:flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    height: 20%;
    background-color: snow;
    color: rgb(25, 53, 212);
    
}

.profile-single{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

.profile-single > *{
    margin:0.5rem;
}

.profile-single:hover{
    cursor:pointer;
    background-color: purple;   
}

.profile-name{
    max-width: 10rem;
    min-width: 10rem;

    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.profile-creation{
    display:flex;
    flex-flow: column;
    
    /* justify-content: space-evenly; */
    align-items: center;

    height: 60%;
}

.profile-name-input{
    width: 80%;
    height: 3rem;
    margin-top: 2rem;
    text-align: center;
}

.profile-create-button{
    margin-top: 1rem;
}

.profile-footer{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 10%;
    width: 100%;
}

.divider-line-right{
    border-right: 2px solid black;
}

.divider-line-bottom{
    border-bottom: 2px solid black;
}