.background-fill{
    position: absolute;
    background-color: rgba(0, 0, 0, 0.473);
    width: 100%;
    height: 100%;
}

.folder-creation-window, .popup-window{
    position: absolute;
    
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: flex-start;

    background-color: rgb(25, 53, 212);
    
    left:50%;
    top:50%;

    margin-left: -16rem;
    margin-top: -11rem;

    min-width: 0rem;
    height: 22rem;
    width: 32rem;
}

.popup-content{
    padding-top: 10%;
    padding-bottom: 10%;
    font-size: 2rem;
    max-width: 80%;
}

.folder-name-input{
    width: 50%;
    height: 4rem;
    text-align: center;
    font-size: 2rem;
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.window-title, .popup-title{
    font-size: 1.5rem;
    padding: 0.2rem;
}